/* src/App.css (VERSÃO FINAL com Redesign de Cards e Responsividade) */

/* ... (Todo o código de variáveis de tema, body, header, etc., que já tínhamos, vem aqui) ... */
body.light { --bg-color: #f0f2f5; --text-color: #1c1e21; --header-bg-color: #ffffff; --card-bg-color: #ffffff; --accent-color: #E50914; --shadow-color: rgba(0, 0, 0, 0.1); --highlight-bg-color: #e4e6eb; --border-color: #ddd; --tagline-color: #65676b; --scrollbar-track-color: #f0f0f0; --scrollbar-thumb-color: #ccc; }
body.dark { --bg-color: #141414; --text-color: #e4e6eb; --header-bg-color: #202020; --card-bg-color: #222; --accent-color: #E50914; --shadow-color: rgba(0, 0, 0, 0.5); --highlight-bg-color: #333; --border-color: #333; --tagline-color: #ccc; --scrollbar-track-color: #202020; --scrollbar-thumb-color: #555; }
body { font-family: 'Poppins', sans-serif; background-color: var(--bg-color); color: var(--text-color); margin: 0; transition: background-color 0.3s, color 0.3s; }
.App header { background-color: var(--header-bg-color); border-bottom: 1px solid var(--border-color); padding: 10px 40px; position: sticky; top: 0; z-index: 1000; display: flex; align-items: center; justify-content: space-between; transition: background-color 0.3s; }
.logo-link { text-decoration: none; color: inherit; }
.App h1 { color: var(--accent-color); margin: 0; font-size: 28px; }
main { padding: 20px; }
main h2 { text-align: center; margin-bottom: 30px; }
.nav-controls { display: flex; align-items: center; gap: 15px; }
.nav-link { color: var(--text-color); text-decoration: none; font-size: 18px; font-weight: bold; padding: 10px 20px; border-radius: 20px; transition: background-color 0.3s ease; }
.nav-link:hover { background-color: var(--highlight-bg-color); }
.theme-toggle-btn { background: none; border: none; font-size: 28px; cursor: pointer; padding: 0 10px; color: var(--text-color); }
.search-form { margin-top: 20px; display: flex; justify-content: center; }
.search-form input { width: 50%; padding: 10px; border-radius: 20px 0 0 20px; font-size: 16px; outline: none; background-color: var(--bg-color); color: var(--text-color); border: 1px solid var(--border-color); }
.search-form button { padding: 10px 20px; border: none; background-color: var(--accent-color); color: white; border-radius: 0 20px 20px 0; font-size: 16px; cursor: pointer; }
.movie-list { display: grid; grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); gap: 20px; justify-items: center; }
.no-movies { grid-column: 1 / -1; text-align: center; padding: 50px; }
.loading { text-align: center; font-size: 24px; padding: 50px; }
.movie-details-backdrop { background-size: cover; background-position: center; position: relative; min-height: 90vh; display: flex; align-items: center; }
.movie-details-backdrop::before { content: ''; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.7); backdrop-filter: blur(5px); }
.movie-details-page { position: relative; z-index: 2; display: flex; padding: 40px; gap: 40px; }
.details-poster img { border-radius: 12px; box-shadow: 0 8px 24px var(--shadow-color); max-width: 300px; }
.details-info h1 { margin-top: 0; font-size: 42px; }
.details-info .tagline { font-style: italic; color: var(--tagline-color); margin-top: -20px; font-size: 18px; }
.details-info h3 { font-size: 24px; border-bottom: 2px solid var(--accent-color); padding-bottom: 5px; display: inline-block; }
.details-info p { font-size: 16px; line-height: 1.6; }
.rating-and-favorite { display: flex; align-items: center; gap: 20px; margin: 20px 0; }
.favorite-btn { width: 60px; height: 60px; font-size: 24px; padding: 0; display: flex; align-items: center; justify-content: center; border-radius: 50%; cursor: pointer; transition: all 0.3s ease; background-color: var(--highlight-bg-color); color: var(--text-color); border: 2px solid var(--text-color); }
.favorite-btn:hover { background-color: var(--text-color); color: var(--bg-color); }
.favorite-btn.favorited { background-color: var(--accent-color); border-color: var(--accent-color); color: #fff; }
.favorite-btn.favorited:hover { transform: scale(1.1); border-color: #ff3b47; background-color: #ff3b47; }
/* --- Redesign dos Cards --- */
.movie-card { border-radius: 8px; overflow: hidden; width: 200px; transition: all 0.3s ease; box-shadow: 0 4px 8px var(--shadow-color); text-decoration: none; color: inherit; position: relative; display: block; }
.movie-card:hover { box-shadow: 0 8px 16px var(--shadow-color); transform: scale(1.05); }
.movie-card img { width: 100%; height: 300px; object-fit: cover; display: block; }
.movie-card-info { position: absolute; bottom: 0; left: 0; right: 0; padding: 16px; background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 50%, rgba(0, 0, 0, 0)); color: #fff; opacity: 0; transform: translateY(20%); transition: opacity 0.3s ease, transform 0.3s ease; }
.movie-card:hover .movie-card-info { opacity: 1; transform: translateY(0); }
.movie-card-info h3 { font-size: 16px; margin: 0 0 8px 0; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.movie-card-info p { font-size: 14px; margin: 0; font-weight: bold; color: #f5c518; }
.movie-card-info p span { font-weight: normal; color: #fff; }
/* --- Barra de Rolagem --- */
::-webkit-scrollbar { width: 12px; }
::-webkit-scrollbar-track { background: var(--scrollbar-track-color); border-radius: 10px; }
::-webkit-scrollbar-thumb { background-color: var(--scrollbar-thumb-color); border-radius: 10px; border: 3px solid var(--scrollbar-track-color); }
::-webkit-scrollbar-thumb:hover { background-color: var(--accent-color); }

/* ======================================================= */
/* --- REGRAS PARA DISPOSITIVOS MÓVEIS (RESPONSIVIDADE) --- */
/* ======================================================= */

@media (max-width: 768px) {
  .App header {
    flex-direction: column; /* Empilha o logo e a navegação */
    gap: 15px;
    padding: 15px;
  }

  .search-form {
    /* Faz a busca ocupar mais espaço e não ficar no topo */
    margin-top: 0;
    margin-bottom: 20px; 
  }

  .search-form input {
    width: 70%;
  }

  .movie-details-page {
    flex-direction: column; /* Empilha o poster e as informações */
    padding: 20px;
    gap: 20px;
  }

  .details-poster img {
    width: 100%;
    max-width: 100%; /* Garante que a imagem não ultrapasse a tela */
    height: auto;
  }

  .details-info h1 {
    font-size: 28px; /* Diminui o tamanho do título */
  }

  .details-info h3 {
    font-size: 20px;
  }
}